@font-face {
  font-family: Vazir;
  src: url("asset/fonts/vazir/Vazir.eot");
  src: url("asset/fonts/vazir/Vazir.eot?#iefix") format("embedded-opentype"),
    url("asset/fonts/vazir/Vazir.woff") format("woff"),
    url("asset/fonts/vazir/Vazir.ttf") format("truetype"),
    url("asset/fonts/vazir/Vazir.woff2") format("woff2");
  font-weight: normal;
}

body {
  margin: 0;
  font-family: Vazir, -apple-system, BlinkMacSystemFont, "Tahoma", "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1d1d1d;
  color: rgb(221, 221, 221);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fontSize {
  font-size: 93% !important;
  font-weight: normal;
}

.centerBlock {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.ingredientContainer {
  width: 180px;
  height: 195px;
  margin: 8px;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  background-color: #494949;
  color: #d3d3d3;
  font-size: 90% !important;
  cursor: pointer;
  position: relative;
}

.ingredientImg {
  width: 105px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 8px;
  margin-top: 8px;
  display: block;
}

.slick-next {
  background-color: #6b6b6b !important;
  background-image: none;
  width: 45px !important;
  height: 45px !important;
  color: white !important;
  box-shadow: 3px 2px 15px #383838;
  margin-right: 10px !important;
  margin-top: 20px;
}

.slick-prev {
  background-color: #6b6b6b !important;
  background-image: none;
  width: 45px !important;
  height: 45px !important;
  color: white !important;
  box-shadow: -3px 2px 15px #383838;
  z-index: 100 !important;
  margin-top: 20px;
}

.slick-next:active {
  width: 43px !important;
  height: 43px !important;
  margin-right: 9px !important;
}

.slick-prev:active {
  width: 43px !important;
  height: 43px !important;
}

.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  width: 45px !important;
  height: 45px !important;
}

.slick-prev:before {
  content: ">" !important;
}
[dir="rtl"] .slick-prev:before {
  content: ">" !important;
}

.slick-next:before {
  content: "<" !important;
}
[dir="rtl"] .slick-next:before {
  content: "<" !important;
}

.slick-prev:before {
  font-family: "Neuton", serif !important;
  font-size: 42px !important;
  line-height: 0.7 !important;
  margin-left: 15px;

  opacity: 1;
  color: rgb(253, 216, 5) !important;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-next:before {
  font-family: "Neuton", serif !important;
  font-size: 42px !important;
  line-height: 0.7 !important;
  margin-left: 12px;

  opacity: 1;
  color: rgb(253, 216, 5) !important;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots li button:before {
  font-size: 10px !important;
  line-height: 10px !important;
}
.slick-dots li.slick-active button:before {
  opacity: 1 !important;
  color: rgb(253, 93, 18) !important;
}
.unslicked {
  display: block;
}

.selectionExceeded {
  width: 180px;
  height: 200px;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgb(7, 7, 7, 0.4);
  z-index: 40;
  cursor: default;
}

.selectedIngContainer {
  width: 180px;
  height: 200px;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 40;
  cursor: default;
}
.selectIngredient {
  position: absolute;
  top: 8px;
  left: 25px;
  width: 131px;
  height: 131px;
  border: solid #04aa1b 2px;
}
.selectedMark {
  box-shadow: -3px 2px 15px #383838;
  color: #494949;
  font-size: 24px;
  text-align: center;
  padding-top: 4px;
}

.selectedMarkGreen {
  position: absolute;
  top: 8px;
  left: 8px;
  background-color: #18aa2b;
  width: 28px;
  height: 28px;
}
.selectedMarkGreen .material-icons {
  font-size: 1.5rem;
}

.selectedMarkOrange {
  position: absolute;
  top: 108px;
  left: 118px;
  width: 37px;
  height: 37px;
  background-color: rgb(243, 97, 39);
  box-shadow: 4px 3px 20px #0f0f0f;
  color: #292929;
  cursor: pointer;
}

.selectedMarkMessage {
  position: absolute;
  top: 64px;
  left: 35px;
  width: 110px;
  height: 32px;
  font-size: 15px;
  background-color: rgb(253, 216, 5);
}

.centerAbsolute {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btnNext {
  background-color: rgb(243, 97, 39);
  border: solid 2px rgb(236, 87, 27);
  font-size: 0.9rem;
  color: white;
  box-shadow: 4px 3px 18px #222222;
}
.btnNext:hover {
  background-color: rgb(224, 75, 16);
  color: white;
}

.btnPrev {
  background-color: rgb(99, 99, 99);
  border: solid 2px rgb(82, 82, 82);
  font-size: 0.9rem;
  color: white;
  box-shadow: 4px 3px 18px #222222;
}
.btnPrev:hover {
  background-color: rgb(73, 73, 73);
  color: white;
}
.btnNext.disabled,
.btnPrev.disabled,
.btnNext.disabled:hover,
.btnPrev.disabled:hover {
  cursor: default;
  color: rgb(197, 197, 197);
}

.btnNext.disabled,
.btnNext.disabled:hover {
  background-color: rgb(204, 96, 54);
}
.btnPrev.disabled,
.btnPrev.disabled:hover {
  background-color: rgb(146, 145, 145);
}

.btnBasket {
  background-image: linear-gradient(to bottom, #ccc, #888);
  font-size: 0.9rem;
  color: rgb(31, 31, 31);
  box-shadow: 4px 3px 18px #222222;
}
.btnBasket:hover {
  background-image: linear-gradient(to bottom, #aaa, #666);
  color: rgb(17, 17, 17);
}

.link,
.link:hover {
  text-decoration: none;
}

.bannerTag {
  display: inline-block;
  font-size: 0.9rem;
}
.bannerTagDisable {
  background-color: rgb(141, 141, 141);
  color: rgb(213, 213, 213);
  border: solid rgb(175, 175, 175) 2px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.bannerTagCurrent {
  background-color: rgb(253, 216, 5);
  background-image: linear-gradient(
    to bottom,
    rgb(253, 203, 40),
    rgb(253, 221, 40),
    rgb(253, 203, 40)
  );
  color: rgb(77, 38, 1);
  border: solid rgb(250, 229, 113) 2px;
  box-shadow: 2px 2px 5px #1f1f1f;
  padding-top: 5px;
  padding-bottom: 5px;
}
.bannerTagPassed {
  background-color: rgb(218, 218, 218);
  color: rgb(42, 43, 42);
  border: solid #049417 2px;
  box-shadow: 4px 3px 5px #1f1f1f;
  padding-top: 3px;
  padding-bottom: 3px;
}
.bannerBg {
  background-color: rgb(107, 107, 107);
  background-image: repeating-linear-gradient(
    -55deg,
    #2b2b2b,
    #2b2b2b 2px,
    #363636 2px,
    #363636 4px
  );
  border-bottom: solid rgb(31, 31, 31) 1px;
  box-shadow: 4px 3px 5px #161616;
}

.BannerPassedIcon {
  color: #049417;
  font-size: 1.2rem;
  margin-right: 5px;
  margin-left: 0px;
}

@media (min-width: 850px) {
  .banner {
    margin-left: 4%;
    margin-right: 4%;
  }
}
.bannerLine {
  position: absolute;
  width: 100%;
  height: 0px;
  border-bottom: solid rgb(175, 175, 175) 2px;
  top: 50%;
  left: 0%;
  z-index: 0;
}
.Modal {
  position: fixed;
  z-index: 500;
  background-color: rgb(253, 244, 236);
  color: #1d1d1d;
  width: 70%;
  border: 2px solid rgb(201, 68, 15);
  box-shadow: 1px 1px 1px black;
  text-align: right;
  left: 15%;
  top: 40%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
  font-size: 0.95rem;
}

@media (min-width: 600px) {
  .Modal {
    width: 600px;
    left: calc(50% - 300px);
  }
}
.Backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 300;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.ModalHeader {
  background-color: rgb(230, 76, 16);
  color: rgb(252, 249, 249);
  font-size: 0.95rem;
}
.pizzaPan {
  width: 100%;
  min-width: 200px;
  max-width: 350px;
}
.stepTitle {
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1rem;
  color: #f0f0f0;
}
.stepExp {
  font-size: 0.95rem;
  font-weight: lighter;
  text-align: center;
  color: #c4c4c4;
  line-height: 1.6rem;
}
.stepExpBox {
  font-size: 0.95rem;
}

div nav {
  background: transparent;
}

.header {
  min-height: 210px;
  height: max-content;
  background: url("./asset/images/bg.jpg") left top no-repeat;
  border: solid rgb(37, 37, 37) 1px;
}

.headerLogo {
  width: 180px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.footer {
  background: url("./asset/images/footerbg.jpg") left top no-repeat;
  background-size: cover;
  font-size: 0.95rem;
}
.footerInfo {
  text-align: right;
  filter: drop-shadow(10px, 10px, 3px, rgba(0, 0, 0, 0.3));
}
.footerLine {
  border-bottom: solid 1px rgb(156, 156, 156);
}
.footerTxtOrange {
  color: rgb(230, 76, 16);
}
.bgGradient {
  background-image: linear-gradient(to bottom, #222222, #1d1d1d, #222222);
}
.pageMargin {
  margin-left: 5%;
  margin-right: 5%;
}
@media (max-width: 430px) {
  .pageMargin {
    margin-left: 1%;
    margin-right: 1%;
  }
}
.footerMenu {
  font-size: 1rem;
}
.footerbtn {
  font-size: 0.85rem;
  background-image: linear-gradient(
    to left,
    rgb(223, 171, 2),
    rgb(252, 82, 139),
    rgb(171, 32, 252)
  );
  color: white;
  border: none;
}
.footerbtn:hover {
  color: rgb(253, 209, 209);
}
.price {
  background-color: #383838;
  text-align: center;
  color: #9b9b9b;
}
.orderBg {
  background-color: #494949;
}
.orderPizzaContainer {
  width: 130px;
  height: 130px;
  top: 0;
  left: 0;
}
.orderFrame {
  margin-left: 0px;
  margin-right: 0px;
}

@media (min-width: 600px) {
  .orderFrame {
    margin-left: 5%;
    margin-right: 5%;
  }
}
.plusNumber {
  background-color: #6b6b6b;
  color: rgb(253, 216, 5);
  vertical-align: text-bottom;
  text-align: center;
  width: 25px;
  height: 25px;
  margin-left: 3px;
  margin-right: 3px;
  padding-top: 2px;
  cursor: pointer;
}
.plusNumber:hover {
  background-color: #585858;
}
.plusNumber:active {
  width: 22px;
  height: 22px;
}
.plusNumber.disable,
.plusNumber.disable:hover,
.plusNumber.disable:active {
  background-color: #3d3d3d;
  color: rgb(116, 101, 22);
  width: 25px;
  height: 25px;
  cursor: default;
}
.textNumber {
  background-color: #272727;
  color: rgb(221, 221, 221);
  text-align: center;
  width: 100%;
}
.textPrice {
  font-size: 0.95rem;
  color: rgb(238, 110, 59);
}
.textbox {
  background-color: #494949;
  border: none;
  border-bottom: solid rgb(238, 110, 59) 1px;
  width: 100%;
  color: #c4c4c4;
  padding: 5px;
}
.shoppingCartBadge {
  top: calc(50% - 25px);
  left: calc(50% + 7px);
  width: 25px;
  height: 25px;
  font-size: 0.8rem;
  padding-top: 5px;
  background-color: rgb(223, 83, 27);
}
.pageLoading {
  background-color: white;
  color: #444;
  height: 80px;
  width: 30%;
  text-align: center;
  text-align: middle;
  border: solid 1px rgb(223, 83, 27);
  position: fixed;
  top: calc(50% - 80px);
  left: calc(50% - 15%);
}

.loader {
  border: 7px solid #dbdbdb;
  border-radius: 50%;
  border-top: 7px solid rgb(223, 83, 27);
  width: 30px;
  height: 30px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
  display: inline-block;
  margin: 10px;
  vertical-align: middle;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loader2 {
  border: 5px solid #727272;
  border-radius: 50%;
  border-top: 5px solid rgb(223, 83, 27);
  width: 35px;
  height: 35px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
}

.pageLoadingTxt {
  font-family: Vazir, -apple-system, BlinkMacSystemFont, "Tahoma", "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  font-size: 1 rem;
}
